        "title": "Excerpt from Oxford Diary (3 Jun 1926)",
        "printedIn": "Peter J. Schakel, <i>Reason and Imagination in C.S. Lewis: A Study of</i> Till We Have Faces (Grand Rapids: Eerdmans)",
